import React, { Component } from 'react'
import { NavBar,Icon } from 'antd-mobile';
import Lunbo from './Carousel'
 
export default class Apphome extends Component {
    render() {
        return (
            <div>
                 <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#ffffff',
                    height:80,fontSize:24}}
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    ]}
                    >住吧家居</NavBar>    
                    {/* <img style={{width:'100%'}} src={require('../img/shouye1.jpg')}></img> */}
                    <Lunbo/>
                    <div style={{width:'100%',height:350,backgroundColor:'#eeeeee'}}>
                        <img style={{float:'left',margin:16,height:300,width:'30%'}} src={require('../img/shouye2.jpg')} alt=''></img>
                        <img style={{float:'left',margin:16,height:300,width:'30%'}} src={require('../img/shouye3.jpg')} alt=''></img>
                        <img style={{float:'left',margin:16,height:300,width:'30%'}} src={require('../img/shouye4.jpg')} alt=''></img>
                    </div>
                    <div style={{height:120,width:'100%',backgroundColor:'#fff'}}>
                        <span style={{fontSize:44,lineHeight:3,marginLeft:30}}>热门推荐</span>
                    </div>
                     <img style={{width:'100%'}} src={require('../img/shouye5.jpg')} alt=''></img>
                     <img style={{width:'100%',paddingTop:15}} src={require('../img/shouye5.jpg')} alt=''></img>
                     <img style={{width:'100%',paddingTop:15}} src={require('../img/shouye5.jpg')} alt=''></img>
            </div>
        )
    }
}
