import React, { Component } from 'react'
import { NavBar,Icon,SearchBar} from 'antd-mobile';
import Storel from './Storel'
 
export default class Store extends Component { 
    render() {
        return (
            <div style={{position:'relative'}}>
                 <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#ffffff',
                    height:80,fontSize:24}}
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    ]}
                    >商城</NavBar> 
                    <div style={{zIndex:999 ,height:60,width:'100%',position:'absolute',top:80,backgroundColor:'#000',opacity:0.5}}>
                        <SearchBar style={{zIndex:999,backgroundColor:'#fff',width:'80%',marginLeft:'10%',marginTop:'1%',borderRadius:5}} placeholder="输入关键字搜索" maxLength={8} />
                    </div>   
                    <Storel/>
                    <div style={{backgroundColor:'white',height:423,width:'100%'}}>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/1.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/2.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/3.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/4.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/5.png')} alt=''></img>
                        <br/>
                        <div>
                            <ul style={{listStyleType:'none'}}>
                                <li style={{float:'left',paddingLeft:'6%',fontSize:36}}>桌</li>
                                <li style={{float:'left',paddingLeft:'16%',fontSize:36}}>床</li>
                                <li style={{float:'left',paddingLeft:'16%',fontSize:36}}>椅</li>
                                <li style={{float:'left',paddingLeft:'16%',fontSize:36}}>几</li>
                                <li style={{float:'left',paddingLeft:'16%',fontSize:36}}>柜</li>
                            </ul>
                        </div>
                        <br/>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/6.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/7.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/8.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/9.png')} alt=''></img>
                        <img style={{width:'19%',paddingTop:15,paddingLeft:'6%'}} src={require('../img/10.png')} alt=''></img>
                        <br/>
                        <div>
                            <ul style={{listStyleType:'none'}}>
                                <li style={{float:'left',paddingLeft:'5%',fontSize:36}}>书架</li>
                                <li style={{float:'left',paddingLeft:'12%',fontSize:36}}>沙发</li>
                                <li style={{float:'left',paddingLeft:'8%',fontSize:36}}>家居饰品</li>
                                <li style={{float:'left',paddingLeft:'5%',fontSize:36}}>户外家具</li>
                                <li style={{float:'left',paddingLeft:'5%',fontSize:36}}>全部品类</li>
                            </ul>
                        </div>
                    </div>
                    <div style={{width:'100%',backgroundColor:'#eee'}}>
                        <div style={{width:'96%',paddingLeft:'2%'}}>
                            <div style={{width:'50%',paddingTop:20,paddingLeft:'2%',float:'left'}}>
                                <img style={{width:'100%'}} src={require('../img/store1.png')} alt=''></img>
                                <span style={{fontSize:34,color:'#939393'}}>Top Art Studio 欧式风格精细...</span>
                                <span style={{fontSize:34,color:'#000',marginTop:20}}>￥39.95</span>
                            </div>
                            <div style={{width:'50%',paddingTop:20,paddingLeft:'2%',float:'left'}}>
                                <img style={{width:'100%'}} src={require('../img/store2.png')} alt=''></img>
                                <span style={{fontSize:34,color:'#939393'}}>顺顺工艺欧式风格塑料外框黑...</span>
                                <span style={{fontSize:34,color:'#000',marginTop:20}}>￥83.99</span>
                            </div>
                            <img style={{width:'98%',paddingLeft:'1%',paddingTop:20}} src={require('../img/store3.png')} alt=''></img>
                        </div>
                    </div>
            </div>
        )
    }
}
