import React, { Component } from 'react'
import { NavBar,Icon } from 'antd-mobile';
import { Tabs, WhiteSpace , WingBlank} from 'antd-mobile';


export default class Linggan extends Component {
    renderContent = tab =>
    (<div style={{ alignItems: 'center',height: '100%', backgroundColor: '#eee' }}>
      <WingBlank>
      <div><img style={{width:'49%',float:'left',height:550}} src={require('../img/lingtu1.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingLeft:'2%'}} src={require('../img/liantu2.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingTop:10}} src={require('../img/liantu3.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingLeft:'2%',paddingTop:10}} src={require('../img/liantu4.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingTop:10}} src={require('../img/lingtu1.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingLeft:'2%',paddingTop:10}} src={require('../img/liantu2.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingTop:10}} src={require('../img/lingtu1.png')} alt=''></img></div>
      <div><img style={{width:'49%',float:'left',height:550,paddingLeft:'2%',paddingTop:10}} src={require('../img/liantu2.png')} alt=''></img></div>
      </WingBlank>
      <p style={{fontSize:44,}}>{tab.title}</p>
    </div>);
    render() {
        const tabs = [
            { title: '推荐' },
            { title: '冬季' },
            { title: '宜家' },
            { title: '小清新' },
            { title: '小户型' },
            { title: '个性色彩' },
            { title: '宜家' },
            { title: '小清新' },
            { title: '小户型' },
          ];
        return (
            <div>
                 <NavBar
                    style={{backgroundColor:'#3fcccb',
                    color:'#ffffff',
                    height:80,fontSize:24}}
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    ]}
                    >灵感</NavBar>  
                    <div>
                        <WhiteSpace />
                        <Tabs tabs={tabs} renderTabBar={props => <Tabs.DefaultTabBar {...props} page={6} />}>
                        {this.renderContent}
                        </Tabs>
                        <WhiteSpace />
                    </div>
  
            </div>
        )
    }
}
