import { TabBar } from 'antd-mobile';
import React from 'react';
import Apphome from './Apphome';
import Linggan from './Linggan';
import Store from './Store'

/**
 *
 *
 * @export
 * @class Apptab
 * @extends {React.Component}
 */
export default class Apptab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }

  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#7a7a7a"//没选中的颜色
          tintColor="#3fcccb"//选中的颜色
          barTintColor="white"//导航条颜色
        >
          <TabBar.Item
            title="首页"//汉字
            key="首页"
            icon={<div style={{//默认
              width: '22px',
              height: '22px',
              backgroundImage: `url(${require('../img/shouye6.png')})`,
              backgroundPosition:'center center',
              backgroundSize:'21px 21px',
              backgroundRepeat:'no-repeat'
            }}
            />
            }
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              backgroundImage:`url(${require("../img/shouye7.png")})` ,
              backgroundPosition: ' center center ',
              backgroundSize:'21px 21px',
              backgroundRepeat:'no-repeat' }}
            />
            }
            selected={this.state.selectedTab === 'blueTab'}
            // badge={1}
            onPress={() => {
              this.setState({
                selectedTab: 'blueTab',
              });
            }}
            data-seed="logId"
          >
           <Apphome/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                backgroundImage: `url(${require('../img/linggan1.png')})`,
                backgroundPosition:'center center',
                backgroundSize:'21px 21px',
                backgroundRepeat:'no-repeat'
              }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                backgroundImage: `url(${require('../img/linggan2.png')})`,
                backgroundPosition:'center center',
                backgroundSize:'21px 21px',
                backgroundRepeat:'no-repeat'
              }}
              />
            }
            title="灵感"
            key="灵感"
            // badge={'new'}
            selected={this.state.selectedTab === 'redTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'redTab',
              });
            }}
            data-seed="logId1"
          >
           <Linggan/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                backgroundImage: `url(${require('../img/store_icon.png')})`,
                backgroundPosition:'center center',
                backgroundSize:'21px 21px',
                backgroundRepeat:'no-repeat'
              }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                backgroundImage: `url(${require('../img/store_icon1.png')})`,
                backgroundPosition:'center center',
                backgroundSize:'21px 21px',
                backgroundRepeat:'no-repeat'
              }}
              />
            }
            title="商城"
            key="商城"
            dot
            selected={this.state.selectedTab === 'greenTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'greenTab',
              });
            }}
          >
           <Store/>
          </TabBar.Item>
          <TabBar.Item
            icon={<div style={{
              width: '32px',
              height: '32px',
              backgroundImage: `url(${require('../img/wode.png')})`,
              backgroundPosition:'center center',
              backgroundSize:'21px 21px',
              backgroundRepeat:'no-repeat'
            }}
            />}
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              backgroundImage: `url(${require('../img/wode1.png')})`,
              backgroundPosition:'center center',
              backgroundSize:'32px 32px',
              backgroundRepeat:'no-repeat'
            }}
            />}
            title="我的"
            key="我的"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
           我的
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

